import {//从types导入一个，不是写在types.js，就不用导入         
	LoadingShow,
	LoadingHide,
	HeaderShow,
	LocalUser,
	SetUserPower,
	SetChildUserInfo
} from './types'

import getters from './getters'//写完了getters.js，再引入

const state={
	loading:false,
	header: true,
	user: {},
	userPower:[],
	childUserInfo:{} //新增子账号时填写的数据
}
const mutations={//接收
	[LoadingShow](state){
		state.loading=true
	},
	[LoadingHide](state){
		state.loading=false
	},
	[HeaderShow](state){
		state.header = false
	},
	[LocalUser](state, param){
		state.user = param
	},
	[SetUserPower](state, param){
		state.userPower = param
	},
	[SetChildUserInfo](state, param){
		state.childUserInfo = param
	}
}


export default{
	mutations,
	state,
	getters
}


