import * as types from './types.js'//全部导入

export default{
	loadingShow:({
		commit
	})=>{
		commit(types.LoadingShow)
	},
	loadingHide:({
		commit
	})=>{
		commit(types.LoadingHide)
	},
	headerShow:({
		commit
	})=>{
		commit(types.HeaderShow)
	},
	localUser:({
		commit
	})=>{
		commit(types.HeaderShow)
	},
	setUserPower:({
		commit
	})=>{
		commit(types.SetUserPower)
	},
	setChildUserInfo:({
		commit
	})=>{
		commit(types.SetChildUserInfo)
	}
}



