import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

import mutations from './mutations'
import actions from './actions'//入口

export default new Vuex.Store({//导出去
	actions,
	modules:{
		mutations//这是一个模块，里面包括了state/mutations
	}
})



