// 全局响应处理


// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
// 模块-Vue
import Vue from 'vue';
// 模块-A级组件
import App from './App';
// 模块-路由管理
import router from './router';
// 模块-全局状态管理
import store from './store/';
// 模块-请求
import axios from 'axios';
// 模块-UI框架
import iView from 'iview';
// import VueClipboard from 'vue-clipboard2';
// 功能组件-回到顶部
import {topY} from '@/utils/date.js';
 // 页面权限管理
import './permission.js';
// 剪切板模块
// Vue.use(VueClipboard);
// iView默认样式库
import 'iview/dist/styles/iview.css';
// iView主题样式库
import '../my-theme/index.less';
// iView自定义样式库（覆盖默认、主题样式库的样式）
import '../my-theme/cover_iview_style.less';
// 引入url解析工具
import urlParam from '../src/utils/requestUtil';
//引入Mint-UI
import MintUI from 'mint-ui';
import 'mint-ui/src/assets/my-style.css';
import {Indicator} from 'mint-ui';
import './rem.js';

// vue-cli默认配置，阻止vue在生存环境下产生提示
Vue.config.productionTip = false;
// 使用iView
Vue.use(iView);
Vue.use(MintUI);
Vue.use(Indicator);
Vue.prototype.$urlParam=urlParam;
function res(response, callback) {
  var res = {
    resEntity: response.data,
    status: response.status
  };
  if (res.status === 200) {
    if (res.resEntity.code === 1003) {
      iView.Message.error(res.resEntity.msg);
      router.push('/login');
    }
  } else {
    iView.Message.error(res.status);
  }
}

// axios基础配置--请求拦截器-发送请求时
axios.interceptors.request.use(function(config) {
  // 状态-请求时，显示loding组件
  // store.dispatch('loadingShow');
  Indicator.open('加载中...');
  // 返回配置信息
  return config;
}, function(error) {
  // 状态-请求错误，隐藏loding组件
  // store.dispatch('loadingHide');
  Indicator.close();
  // 返回错误
  return Promise.reject(error);
});

// axios基础配置--请求拦截器-请求成功时
axios.interceptors.response.use(function(response) {
  res(response);
  // console.log('全局监听http');
  // console.log(response);
  // 状态-请求成功，隐藏loding组件
  // store.dispatch('loadingHide');
  Indicator.close();
  // 返回请求实体
  return response;
}, function(error) {
  // 状态-请求错误，隐藏loding组件
  // store.dispatch('loadingHide');
  Indicator.close();
  // iView.Message.error('网络错误');
  // 返回错误
  return Promise.reject(error);
});
// axios基础配置--请求默认属性设置
// axios.defaults.baseURL = "http://10.0.0.222:8080"; // 请求前缀
// axios.defaults.baseURL = "http://192.168.52.122:8080";
axios.defaults.baseURL = "/api";
//axios.defaults.baseURL = "http://localhost:8080";
axios.defaults.timeout = 10000; // 请求超时
/*axios.defaults.auth = { // 请求凭证
  username: 'chenjin',
  password: '18874293233'
};*/
// axios不是插件，不能使用use，需要添加的Vue类的原型上进行使用
Vue.prototype.$http = axios;


// 把常用方法添加的Vue类的原型上
Vue.prototype.$iView = iView;
Vue.prototype.$BackTop = topY;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
});




