<template>
  <div id="app">
    <div class="content-div">
      <!--<transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOut">-->
      <router-view/>
      <!--</transition>-->
    </div>
  </div>
</template>

<script>
  export default {
    name: 'app',
    beforeCreate:function () {
      let userCode = this.$urlParam.getUrlKey("code");
      this.$store.commit('LocalUser',{userCode:userCode})
      if (userCode) {
        this.$http.post("/ykym-web/checkLogin", {userLoginCode: userCode})
          .then(
            (response) => {
              let type = response.data.status;
              if (type == 'userLogin') {
                console.log('信息录入')
                this.$router.push('/userLogin');
              } else if (type == 'userPhoneLogin') {
                console.log('信息录入')
                this.$store.commit('LocalUser',{userCode:userCode, userPhones:response.data.phone})
                this.$router.push('/userPhoneLogin');
              } else if (type == 'userIsLogin'){
                if(this.$store.getters.user.userCode){
                  this.$router.push('/userIndex');
                } else {
                  alert("用户信息获取失败，请重新登录！！！")
                  this.$router.push('/Test');
                }
              } else {
                alert(response.data.msg)
                this.$router.push('/Test');
              }
            },
            (error) => {
              console.log(error);
              this.$router.push('/Test');
            }
          )
      } else {
        alert("未授权访问!!!")
        this.$router.push('/Test');
      }
    }
  }
</script>

<style>
  #app {
    margin-top: 60px;
  }
</style>



