export default{
	loading:(state)=>{
		return state.loading
	},
  header:(state)=>{
    return state.header
  },
  user:(state) => {
    return state.user
  },
  userPower:(state) => {
    return state.userPower
  },
  childUserInfo:(state) => {
    return state.childUserInfo
  }
}


